import React from 'react'
import UserData from './user_data/UserData';
import { observer } from 'mobx-react';
import LoginForm from './LoginForm';
import InputField from './InputField';
import SubmitButton from './SubmitButton';
import './App.css';

class App extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: {}
        };
    }

    async componentDidMount() {
        try {
            let get_result = await fetch('/isLoggedIn', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });

            let result = await get_result.json();

            if(result && result.success) {
                UserData.loading = false;
                UserData.isLoggedIn = true;
                UserData.username = result.username;
                this.fetchData();
            }
            else {
                UserData.loading = false;
                UserData.isLoggedIn = false;
            }
        }

        catch(e) {
            UserData.loading = false;
            UserData.isLoggedIn = false;
        }
    }

    async doLogout() {
        try {
            let get_result = await fetch('/logout', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });

            let result = await get_result.json();

            if(result && result.success) {
                UserData.isLoggedIn = false;
                UserData.username = '';
            }
        }

        catch(e) {
            console.log(e);
        }
    }

    async fetchData() {
        let url = "https://api.github.com/repositories/19438/issues";
        let fetch_json = await fetch(url);
        const res = await fetch_json.json();
        this.setState({
            data: res
        });
    }

    render() {

        if(UserData.loading) {
            return (
                <div className="app">
                    <div className="container">
                        Loading, please wait...
                    </div>
                </div>
            );
        }

        else {
            if(UserData.isLoggedIn) {
                return (
                    <div className="app">
                        <div className="container">
                            This post has { this.state.data[0].comments }.
                            <SubmitButton
                                text={ 'Logout' }
                                disabled = { false }
                                onClick = { () => this.doLogout() }
                            />
                        </div>
                    </div>
                );
            }
            return (
                <div className="app">
                    <div className="container">
                        <LoginForm/>
                    </div>
                </div>
            );
        }
    }
}

export default observer(App);
